@use "../abstracts/breakpoints";
@use "../abstracts/colors";

#page-home {
  main {
    margin: 0 auto 5vh;
  }

  #section-hero {
    height: 70vh;
    position: relative;
    background-image: url("/img/lashlift-browlift-microblading-i-vasastan.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 0;

    .section-hero--cta {
      position: absolute;
      bottom: 2rem;
      left: 5rem;
    }
  }

  #section-about {
    font-size: calc((1.5 - 1) * 1.2vw + 1rem);
  }

  #section-services {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 17px;
    grid-row-gap: 0px;

    .section-services__service-block {
      aspect-ratio: 1 / 1; // Finns inget universellt stöd, avveckla
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 17px;

      .service-block__content-wrapper {
        width: 70%;
        * {
          display: block;
          width: fit-content;
        }
        span {
          font-size: calc((2.2 - 1) * 1.2vw + 1rem);
          color: colors.$bg-alt-2;
          font-weight: 700;
        }
      }
    }
    .service-block__boka-tid {
      background-image: url("/img/microblading-stockholm-vasastan-skönhetssalong.jpg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    .service-block__behandlingar {
      background-image: url("/img/stockholm-skönhetssalong-klinik-microblading.jpg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  #section-contact {
    input:not([type="submit"]),
    textarea {
      width: 100%;
    }
  }
}

@media only screen and (max-width: breakpoints.$phone) {
  #page-home {
    #section-services {
      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: 2fr;
      grid-column-gap: 17px;
      grid-row-gap: 0px;
    }
  }
}
