@use "../abstracts/breakpoints";
@use "../abstracts/colors";
#main-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: colors.$bg-main;
  padding: 3.3vmax 4vw;

  div {
    width: 20%;
    *:not(:first-child) {
      margin-top: 17px;
      display: block;
    }
  }

  &__nav {
    span {
      font-weight: 700;
    }
    li {
      font-weight: 400;
      margin: 1rem 0;
      &.active a {
        text-decoration: underline;
      }
    }
  }

  &__logo {
    height: auto;
    max-width: 200px;
  }
}

@media only screen and (max-width: breakpoints.$phone) {
  #main-footer {
    flex-direction: column;
    padding: 17px;

    div {
      width: 100%;
      max-width: 100%;
    }
    &__logo {
      display: block;
      margin: 0 auto;
      width: 300px;
      max-width: 100%;
      margin-bottom: 5rem;
    }
  }
}
