@use "../abstracts/breakpoints";
@use "../abstracts/colors";

.main-header {
  background-color: colors.$bg-main;
  .main-header__wrapper {
    height: 160px;
    display: flex;
    padding: 0 4vw;
    flex-basis: 100%;
    justify-content: space-between;
    align-items: center;

    .logo {
      display: flex;
      width: 100%;
      max-width: 125px;
    }
  }
}

// Mobile menu-button animation
body.menu-open .main-nav__mobile-btn {
  .main-nav__mobile-btn__bars {
    background-color: transparent;
    &:before {
      transition: 0.15s;
      transform: rotate(-135deg);
    }

    &:after {
      transition: 0.15s;
      transform: rotate(135deg);
    }
  }
}

.main-nav__desktop {
  & > ul {
    display: flex;
    align-items: center;

    & > li {
      display: block;
      &:not(:first-child) {
        margin-left: 1.6vw;
      }
    }
  }

  .active {
    text-decoration: underline;
  }

  .dropdown:hover ul,
  .dropdown a:focus ~ ul {
    visibility: visible;
  }

  .dropdown--active {
    background-color: colors.$bg-alt-1;
  }

  .dropdown {
    display: flex;
    position: relative;
    align-items: center;
    height: 160px;
    &:hover,
    a:focus {
      background-color: colors.$bg-alt-1;
    }
  }

  .dropdown .dropdown--display {
    visibility: visible;
  }

  .dropdown > a,
  .dropdown > ul {
    padding: 0 1rem;
  }

  .dropdown ul {
    background-color: colors.$bg-alt-1;
    visibility: hidden;
    flex-direction: column;
    min-width: 5rem;
    position: absolute;
    transition: all 0.01s ease;
    z-index: 1;
    top: 120px;

    li {
      display: block;
      margin: 0;
      &:not(:first-child) {
        margin-top: 0.75rem;
      }
      &:last-of-type {
        margin-bottom: 1rem;
      }
    }
  }
}

.main-nav__mobile {
  z-index: 1;
  opacity: 0;
  overflow: scroll;
  height: 100vh;
  padding: 0 4vw;

  & > ul {
    padding-left: 6px;
    transform: translatey(20px);
    display: flex;
    flex-direction: column;
    gap: clamp(1rem, 2vmin, 2vmin);
    transition: transform 600ms cubic-bezier(0.4, 0, 0.2, 1);

    li,
    .fab {
      font-size: 6vmin;
      text-decoration: none;
    }
  }

  ul li ul li {
    font-size: clamp(1rem, 3.5vmin, 3rem);
  }
}

.main-nav__mobile,
.main-nav__desktop {
  display: none;
}

.main-nav__mobile-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  min-height: 22px;
  padding-left: 6px;
  padding-right: 6px;
  border: none;
  background-color: transparent;
  color: inherit;
  cursor: pointer;
  transition: 0.3s ease;

  .main-nav__mobile-btn__bars {
    display: block;
    position: relative;
    width: 35px;
    height: 1px;
    background-color: colors.$text;
    transition: 0.3s;

    &:before,
    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: colors.$text;
      transition: 0.3s;
    }

    &:before {
      transform: translate(0, -10px);
    }

    &:after {
      transform: translate(0, 10px);
    }
  }
}

@media (min-width: breakpoints.$tablet) {
  .main-header {
    .main-nav__desktop {
      display: inline;
    }

    .main-nav__mobile-btn {
      display: none;
    }
  }
}
@media (max-width: breakpoints.$tablet) {
  body.menu-open {
    overflow: hidden;

    //   .main-nav__mobile {
    //     ul {
    //       transform: translatey(20px);
    //       // transition: transform 600ms cubic-bezier(0.4, 0, 0.2, 1);
    //       // transition: visibility 600ms cubic-bezier(0.4, 0, 0.2, 1),
    //       //   opacity 400ms cubic-bezier(0.4, 0, 0.2, 1);

    //       li {
    //       }
    //     }
    //   }
    // }

    .main-nav__mobile {
      animation: fadein 250ms;
      opacity: 1;
      background-color: colors.$bg-main;
      display: block;
      position: absolute;
      right: 0;
      width: 100vw;
      ul {
        transform: translatey(0);
      }
    }

    @keyframes fadein {
      from {
        opacity: 0;
      }
      to {
        opacity: 1;
      }
    }
  }

  .main-header {
    position: relative;

    .logo {
      display: flex;
      flex: 1 0 calc(100% - 50px);
      align-items: center;
      flex-wrap: nowrap;
      align-items: center;
      justify-content: center;
    }

    .main-nav__desktop {
      display: none;
    }

    .main-mobile {
      opacity: 0;
      animation: fadeout 250ms;

      @keyframes fadeout {
        0% {
          opacity: 1;
        }
        99% {
          opacity: 0;
        }
        100% {
          display: none;
        }
      }
    }
  }
}
