.form {
  input:not([type="submit"]),
  textarea {
    display: block;
    width: 100%;
    padding: 12px;
    margin: 6px 0 4px;
    border: 1px solid #ccc;
    background: #fafafa;
    color: #000;
    font-family: sans-serif;
    font-size: 12px;
    line-height: normal;
    box-sizing: border-box;
    border-radius: 2px;
    margin-bottom: 24px;
  }

  label > input:required::after {
    content: "*";
  }
}
