@use "../abstracts/colors";
@use "../abstracts/breakpoints";

.card + .card {
  margin-top: 5vh;
}

.card {
  box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.1);
  display: flex;
  width: 1280px;
  max-width: 100%;
  margin: 0 auto;

  &:nth-of-type(3n) {
    background-color: colors.$bg-main;
  }
  &:nth-of-type(3n + 1) {
    background-color: colors.$bg-alt-2;
  }
  &:nth-of-type(3n + 2) {
    background-color: colors.$bg-alt-1;
  }
}

.card__col-padding {
  padding: 2rem 1.5rem;
}

.card-col--img {
  display: flex;
  picture {
    display: flex;
  }
}

.card__col-4 {
  width: calc(100% / 12 * 5);
}

.card__col-8 {
  width: calc(100% / 12 * 7);
}

.card-col__footer {
  text-align: center;
  font-size: 1.2rem;
}

@media only screen and (max-width: breakpoints.$tablet) {
  .card__col-4 {
    width: 50%;
  }

  .card__col-8 {
    width: 50%;
  }
}

@media only screen and (max-width: breakpoints.$phone) {
  .card {
    flex-direction: column;
  }

  .card__col-4 {
    width: 100%;
  }

  .card__col-8 {
    width: 100%;
  }

  .card-col--img {
    order: -1;
  }
}
