@use "../abstracts/breakpoints";

.section {
  margin: 0 auto;
}

.section--std-padding {
  padding: 6vmax 4vw;
}

.section--w-100 {
  max-width: 100%;
}
.section--w-75 {
  max-width: 75%;
}
.section--w-50 {
  max-width: 50%;
}

@media only screen and (max-width: breakpoints.$phone) {
  .section--w-100 {
    max-width: 100%;
  }
  .section--w-75 {
    max-width: 100%;
  }
  .section--w-50 {
    max-width: 100%;
  }
}
