@use "../abstracts/colors";
@use "../abstracts/types";
@use "../abstracts/breakpoints";

html {
  font-size: 18px;
}

html * {
  font-family: types.$main;
  font-weight: 400;
  line-height: 1.75;
  color: colors.$text;
}

p,
strong,
b,
i,
em,
span,
a,
label,
textarea,
input {
  font-size: 100%;
  max-width: 100%;
}

p {
  margin-bottom: 1rem;
  margin-top: 1rem;
  &:first-child {
    margin-top: 0;
  }
}

h1,
h2,
h3,
h4,
h5 {
  margin-bottom: 1rem;
  font-weight: 500;
  line-height: 1.3;
}

h1 {
  margin-top: 0;
  font-size: 2rem;
}

h2 {
  font-size: 1.7rem;
}

.lead,
.fab {
  font-size: 1.2rem;
}

small,
.text_small {
  font-size: 0.833rem;
}

a {
  color: colors.$text;
  text-decoration: none;
}

b,
strong,
.bold {
  font-weight: 600;
}

.text-align--center {
  text-align: center;
}

img {
  object-fit: cover;
  height: auto;
  max-width: 100%;
  display: flex;
}

.flex {
  display: flex;
  gap: 1rem;

  & > div {
    flex-basis: 50%;
  }
}

@media only screen and (max-width: breakpoints.$phone) {
  .flex {
    flex-direction: column;

    & > div {
      flex-basis: 100%;
    }
  }
}
